<script>
import ComponentCarousel from './../components/ComponentCarousel';
export default {
    components: {
        ComponentCarousel,
    },
    computed: {
        homeData() {
            return this.$store.state.home.homeData;
        },
        commonData() {
            return this.$store.state.common.commonData;
        },
    },
    data() {
        return {
            tabsActive: 'first',
            gameStatus: '1',
            gameType: '1',
        }
    },
    mounted() {
        let { gameStatus, gameType } = this.$route.query;
        if (gameStatus) this.gameStatus = gameStatus;
        if (gameType) this.gameType = gameType;

        // this.utils.http("v1/invite/log", {

        // }).then(response => {
        //     console.log(response);
        // })
        // this.$store.commit("HOME_LOAD", {
        //     home: 'data'
        // });
    },
    methods: {
        handleClick(item) {
            console.log(item);
        },
        load () {
            // 异步
            this.$store.dispatch("HOME_LOAD", {
                home: 'data'
            })
            .then(response => {
                console.log(response);
            })
            // 同步
            // this.$store.commit("COMMON_DATA", {
            //     home: '我是公共数据common'
            // })
        },
        toggleGameStatus() {
            // 游戏状态切换
            // 切换状态，改变url参数
            this.utils.modifyURIParam(this, {
                gameStatus: this.gameStatus
            });
        },
        toggleGameType() {
            this.utils.modifyURIParam(this, {
                gameType: this.gameType
            });
        },
    },
}
</script>
