// 前缀必须是文件名
import utils from '../../../utils/common';
export const SETTLED_DATA = 'SETTLED_DATA';
export const UNSETTLED_DATA = 'UNSETTLED_DATA';

export default {
    state: {
        result: '', //储存相关数据
    },
    getters: {

    },
    mutations: {
        [SETTLED_DATA](state, data) {
            if (data) { console.log() }
        },
        [UNSETTLED_DATA](state, data) {
            if (data) { console.log() }
        },
    },
    // 异步操作
    actions: {
        [SETTLED_DATA]({ commit }, data) {
            return new Promise((resolve) => {
                utils.request('getSettlementBetRecord', data, 'djBet').then(response => {
                    resolve(response.data);
                });
                commit(SETTLED_DATA, data)
            })
        },
        [UNSETTLED_DATA]({ commit }, data) {
            return new Promise((resolve) => {
                utils.request('getNoSettlementBetRecord', data, 'djBet').then(response => {
                    resolve(response.data);
                });
                commit(UNSETTLED_DATA, data)
            })
        }
    }
}