import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'
import home from './modules/home';
import register from './modules/register';
import login from './modules/login';
import task from './modules/task';
import retrievePassword from './modules/retrievePassword';
import common from './modules/common';
import mySetting from './modules/my/mySetting';
import myInfo from './modules/my/myInfo';
import recharge from './modules/wallet/recharge';
import exchange from './modules/wallet/exchange';
import dealRecord from './modules/wallet/dealRecords';
import orderRecord from './modules/wallet/orderRecords';
import pay from './modules/wallet/pay';
import active from './modules/user/active';
import systemMsg from './modules/user/systemMsg';
Vue.use(Vuex)
export default new Vuex.Store({
    //模块引入
    modules: {
        user,
        home,
        register,
        common,
        mySetting,
        myInfo,
        login,
        retrievePassword,
        recharge,
        exchange,
        dealRecord,
        orderRecord,
        pay,
        task,
        active,
        systemMsg
    }
});