// 前缀必须是文件名
import utils from '../../../utils/common';
export const DEAL_DATA = 'DEAL_DATA';
export default {
    state: {
        result: '', //储存相关数据
    },
    getters: {

    },
    mutations: {
        [DEAL_DATA](state, data) {
            if (data) { console.log() }
        },
    },
    // 异步操作
    actions: {
        [DEAL_DATA]({ commit }, data) {
            return new Promise((resolve) => {
                utils.request('web_get_bill', data).then(response => {
                    resolve(response.data);
                });
                commit(DEAL_DATA, data)
            })
        }
    }
}