// 前缀必须是文件名
import utils from '../../../utils/common.js';
export const SYSTEM_MGS = 'SYSTEM_MGS'
export default {
    state: {
        systemMsgData:{}
    },
    getters: {

    },
    mutations: {
        [SYSTEM_MGS](state, data) {
            // 处理数据
            if (data) {
                state.systemMsgData = data;
                // utils.request('getmsg', data).then(response => {
                //     console.log(response);
                //     if(response.code == 200){
                //         if (response.data) {
                //             // alert()
                //             state.systemMsgData = response.data;
                //         } else {
                //             state.systemMsgData = []
                //         }
                       
                //     }
                // }); 
            }
        },
    },
    actions: {
        // { commit }, data
        // [SYSTEM_MGS]({ commit }, data) {
        //     commit(SYSTEM_MGS, data.data);
        //     //arguments[1]
        //  },
         [SYSTEM_MGS]({ commit }, params) {
            // 取数据
            return new Promise((resolve, project) => {
             utils.request('getmsg', params).then(response => {
                 let { data } = response;
                 if (data.code == 200) {
                    // console.log(data)
                     commit(SYSTEM_MGS, data.data);
                     resolve(data);
                     
                 } else {
                     project(response);
                 }
            });  
            })
         },
    }
}